<template>
<div class="card">
  <div class=card-header>
    Exam02TemplateRefs
  </div>
  <div class="card-body">
    <div>
      <h6>[DOM 요소 참조]</h6>
      <p>data: {{data}}</p>
      <button @click="setInputValue" class="btn btn-info btn-sm mr-2">데이터 변경</button>
      <input type="text" ref="inputTag" name="mid"/>
    </div>
    <div>
      <!--가능하긴 하지만 최대한 이런 방식으로는 코드 작성하지 마십시옹-->
      
      <h6>[자식 컴포넌트 참조]</h6>
      <p>Child Data: {{(childRef!==null)?childRef.data:""}}</p>
      <p>
        <button @click="changeChildData" class="btn btn-info btn-sm mr-2">자식 데이터 변경</button>
        <button @click="callChildMethod" class="btn btn-info btn-sm mr-2">자식 메소드 호출</button>
      </p>
      <Child ref="childRef"/>
    </div>
  </div>
</div>
</template>

<script setup>
import {ref, onMounted} from "vue";
import Child from "./Child.vue";
const data=ref("summer");
const inputTag=ref(null);//현재 setup이라는 hook이 실행되는 시점이기 때문에 아직 객체 생성 전 = > 참조하고 싶어도 참조할 수 있는 태그가 없답니당!
const childRef=ref(null);

onMounted(()=>{
  console.log(inputTag.value);
  inputTag.value.focus();//지정 input 태그에 focus
  console.log(childRef);
});

function setInputValue() {
  inputTag.value.value=data.value;
}
function changeChildData() {
  childRef.value.data+=1;
}
function callChildMethod() {
  childRef.value.method();
}
</script>

<style scoped>

</style>