<template>
<div class="card">
  <div class=card-header>
    Child
  </div>
  <div class="card-body">
    <p>data: {{data}}</p>
  </div>
</div>
</template>

<script setup>
import {ref, defineExpose} from "vue";//defineExpose: 외부로 속성 유출 가능하게함, 근데 하지마세용 나쁜 코드임
const data=ref(10);
function method(){
  data.value-=1;
}
defineExpose({
  data, method
});

</script>

